<template>
  <div class="swipe">

  </div>
</template>


<script>
import axios from 'axios'

export default {
  name: 'SwipeDetect',

  data() {
    return {
      thumpValue: 'Inject Swiper',
    }
  },
  mounted() {
    this.touchListen();
  },
  methods: {
    touchListen() {
      let
          swipeDir,
          startX,
          startY,
          distX,
          distY,
          elapsedTime,
          startTime,
          thumpValue
      const
          threshold = 20, //required min distance traveled to be considered swipe
          restraint = 150, // maximum distance allowed at the same time in perpendicular direction
          allowedTime = 300 // maximum time allowed to travel that distance

      // const youtubeCardsArray = [["dZg1QCiAOrs","eG9hgTz_pdg","GWR2_r-4QDs","OxGyDbyjSP4","3h3XMDCC4RU","VQOY5vDJzr4","R3bqlYPbGdk","UpT2SSn5U5A","3jl3nbuh8Jw","KNFJnIuo2L4","a0Y59qpI6No","QnV9oh4LweA","SLlScHVevSU"],["P0n5k_zJw2A","1ksBEewlmzM","FbLfpl0-SIE","NvEsTZo_Oq0","_qZin41_Erw","p8WhVTOtxuM","Yeri3l6VQj8","YwwJOKnRu7Y","F3KgwOinkv0","5XMt-fqWKTI","_flJjtQHE5c","rdUJdR9D1O0","FGaraJlZrXs"],["cBYFqNtLgQM","L5uz4p0CTi0","4fLfYtSjhto","txhNC98k3m0","OBZ-wY3Tm78","hoZ3xkXAduo","2PgFiekiw1s","kG6b3ZkK0ZA","_3AnagUPyHw","tC4No77nyyg","aYXwKMY7JIY","E3EeMfEt47U","LqLqBp0C6vQ"],["IRvCexyE-nI","pNudc1ps-lM","7XZ6ue3s-gg","p3TJ_NFYjdc","OYgVxt52Mec","q16x1TdCN5Y","z1pyi8OeeH8","uVrZzo8iIIQ","QuVNaklPu_Q","U-6fzHDycic","_oRUOpDUxDs","I319t6rTKwo","gC0IX7RrmGo"]];
      const cardsArray = [["as", "2s", "3s", "4s", "5s", "6s", "7s", "8s", "9s", "xs", "js", "qs", "ks"], ["ah", "2h", "3h", "4h", "5h", "6h", "7h", "8h", "9h", "xh", "jh", "qh", "kh"], ["ac", "2c", "3c", "4c", "5c", "6c", "7c", "8c", "9c", "xc", "jc", "qc", "kc"], ["ad", "2d", "3d", "4d", "5d", "6d", "7d", "8d", "9d", "xd", "jd", "qd", "kd"]];
      let swipeBuffer = [];

      async function  thump(matchString) {
        thumpValue = matchString
        const timeStamp = Math.floor(new Date().getTime()/1000.0);
        await axios.post(`https://11z.co/_w/12001/selection?tm=${timeStamp}`, {
          "value": `https://11z.co/images/houdini/${matchString}.jpg`,
          "thumperId": 5

        })
            .then(async function (response) {
              console.log(response);
              console.log(await axios.post('https://ntfy.sh/Injectswipe', {
                "thumped": matchString
              }));
              navigator.vibrate(200);
            })
            .catch(function (error) {
              console.log(error);
            });

        console.log(thumpValue)
      }

      function getCardValue(swipeBuffer) {
        const suit = swipeBuffer[2];
        const value = `${swipeBuffer[0]}${swipeBuffer[1]}`;
        let suitArray = [];
        let suitItem = 0;

        if(value === "rl" && suit === "r"){
          return "joker"
        }

        switch (suit) {
          case 'u':
            suitArray = cardsArray[0];
            break;
          case 'r':
            suitArray = cardsArray[1];
            break;
          case 'd':
            suitArray = cardsArray[2];
            break;
          case 'l':
            suitArray = cardsArray[3];
            break;
          default:
            break;
        }

        switch (value) {
          case "ur":
            suitItem = 0;
            break;
          case "ru":
            suitItem = 1;
            break;
          case "rr":
            suitItem = 2;
            break;
          case "rd":
            suitItem = 3;
            break;
          case "dr":
            suitItem = 4;
            break;
          case "dd":
            suitItem = 5;
            break;
          case "dl":
            suitItem = 6;
            break;
          case "ld":
            suitItem = 7;
            break;
          case "ll":
            suitItem = 8;
            break;
          case "lu":
            suitItem = 9;
            break;
          case "ul":
            suitItem = 10;
            break;
          case "uu":
            suitItem = 11;
            break;
          case "ud":
            suitItem = 12;
            break;
          default:
            break
        }

        return suitArray[suitItem];
      }

      function handleSwipe(swipeDir) {

        if (swipeDir !== 'none') {
          if (swipeBuffer.length < 3) {
            swipeBuffer.push(swipeDir)
            console.log(swipeBuffer)
          }

          if (swipeBuffer.length === 3) {
            const actualCard = getCardValue(swipeBuffer)
            thump(actualCard);
            // alert(`You swiped ${actualCard}!`)
            swipeBuffer = [];
          }
        }
      }

      document.addEventListener('touchstart', function (e) {
        let touchObj = e.changedTouches[0]
        swipeDir = 'none'
        startX = touchObj.pageX
        startY = touchObj.pageY
        startTime = new Date().getTime() // record time when finger first makes contact with surface

      }, false)
      document.addEventListener('touchend', function (e) {
        let touchObj = e.changedTouches[0]
        distX = touchObj.pageX - startX // get horizontal dist traveled by finger while in contact with surface
        distY = touchObj.pageY - startY // get vertical dist traveled by finger while in contact with surface
        elapsedTime = new Date().getTime() - startTime // get time elapsed
        if (elapsedTime <= allowedTime) { // first condition for awipe met
          if (Math.abs(distX) >= threshold && Math.abs(distY) <= restraint) { // 2nd condition for horizontal swipe met
            swipeDir = (distX < 0) ? 'l' : 'r' // if dist traveled is negative, it indicates left swipe
          } else if (Math.abs(distY) >= threshold && Math.abs(distX) <= restraint) { // 2nd condition for vertical swipe met
            swipeDir = (distY < 0) ? 'u' : 'd' // if dist traveled is negative, it indicates up swipe
          }
        }
        handleSwipe(swipeDir);
      }, false)
      window.addEventListener("scroll", (e) => {
        e.preventDefault();
        window.scrollTo(0, 0);
      });
    },
  },
}
</script>
